<template>
  <div>
    <!---->
    <div class="brandsStore_head">
      <div style="display:flex;justify-content:flex-start;align-items:center;">
        <div class="brandsStore_addStore" @click="addStores"><i class="el-icon-plus"></i>&nbsp;</div>
        <div class="brandsStore_storeNub" style="margin-right:20px;">&nbsp;<span style="color:#ed6f00">80</span></div>
        <div class="brandsStore_chooseCity"></div>
        <el-select v-model="city" class="brandsStore_options" style="width:120px;" placeholder="" @change="chooseCity">
          <el-option
            v-for="item in citysList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <el-input
        style="width:240px;margin-left:20px;"
        size="medium"
        class="brandsStore_search"
        placeholder=""
        prefix-icon="el-icon-search"
        v-model="search"
        @blur="searchStore">
      </el-input>
    </div>
    <!---->
    <el-table
      :data="data"
      class="brandsStore_table"
      style="width: 100%;margin-top:20px;">
      <el-table-column
        type="index"
        align="center"
        label=""
        :width="100"
        :index="indexMethod">
      </el-table-column>
      <el-table-column
        prop="brandsName"
        align="center"
        label="">
      </el-table-column>
      <el-table-column
        prop="storeName"
        align="center"
        label="">
      </el-table-column>
      <el-table-column
        prop="storeManager"
        align="center"
        label="">
      </el-table-column>
      <el-table-column
        prop="phone"
        align="center"
        label="">
      </el-table-column>
      <el-table-column
        prop="address"
        align="center"
        label="">
      </el-table-column>
      <el-table-column
        align="center"
        label="">
        <template slot-scope="scope">
         <p v-for="(items,index) in scope.row.time" :key="index">{{items}}</p>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label="">
        <template slot-scope="scope">
          <el-button style="background-color:#FF9800;color:#fff;padding:7px 20px;" @click="edit(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <pages :pageSize="pageSize" :totalPages="totalPages" @changeCurrentPage="getCurrentPage" />
    <!---->
    <el-dialog title="" top="2vh" :visible.sync="storesBox" width="40%" center>
      <el-form :model="form" :rules="rules" ref="ruleForm" label-width="120px">
        <el-form-item label="" prop="brands">
          <el-select v-model="form.brands" placeholder="">
            <el-option
              v-for="item in brands"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="storeName">
          <el-input v-model="form.storeName" style="width:250px;" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-autocomplete
            v-model="form.address"
            :disabled="true"
            style="max-width:400px"
            placeholder="">
            <i
              class="el-icon-edit el-input__icon"
              slot="suffix">
            </i>
          </el-autocomplete>
        </el-form-item>
        <el-form-item label="" prop="range">
          <el-autocomplete
            v-model="form.range"
            :disabled="true"
            placeholder="">
            <i
              class="el-icon-edit el-input__icon"
              slot="suffix">
            </i>
          </el-autocomplete>
        </el-form-item>
        <el-form-item label="">
          <el-button @click="storeEdit"></el-button>
        </el-form-item>
        <el-form-item label="" prop="peopleMoney">
          <el-input v-model="form.peopleMoney" style="width:170px;" placeholder=""></el-input> 
        </el-form-item>
        <el-form-item label="" prop="storeManager">
          <el-input v-model="form.storeManager" style="width:250px;" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="storePhone">
          <el-input v-model="form.storePhone" style="width:250px;" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="storeImgs">
          <imgs :files="form.storeImgs" class="brandsStore_img" :limitImg="3" @imgFiles="getStoreImg" />  
        </el-form-item>
        <el-form-item label="" prop="storeTimeAm">  
          <el-time-picker
            is-range
            arrow-control
            v-model="form.storeTimeAm"
            size="small"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            placeholder="">
          </el-time-picker>
        </el-form-item>
        <el-form-item label="" prop="storeTimePm">  
          <el-time-picker
            is-range
            arrow-control
            v-model="form.storeTimePm"
            size="small"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            placeholder="">
          </el-time-picker> 
        </el-form-item>
        <el-form-item label="" prop="timeType">
          <el-select v-model="form.timeType" placeholder="">
            <el-option
              v-for="item in timeTypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="initPrice">
          <el-input v-model.number="form.initPrice" style="width:150px;" placeholder=""></el-input> ()
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button style="background-color:#ff3b21;border-color:#ff3b21;color:#fff;padding:7px 20px;border-radius:30px;" type="primary" @click="submit('ruleForm')"> </el-button>
        <el-button style="background-color:#000;color:#fff;padding:7px 20px;border-radius:30px;" @click="resetForm('ruleForm')"> </el-button>
      </div>
      <!---->
      <el-dialog
        top="2vh"
        title=""
        :visible.sync="mapVisible"
        append-to-body
        class="brandsStore_inline_body">
        <maps  v-bind:propsCenter="region" v-bind:propsPath="storeArr"  @storePath="getPath" />
      </el-dialog>
    </el-dialog>
  </div>
</template>

<script>
import pages from './../components/pages.vue'
import imgs from './../components/img.vue'
import maps from './../components/map.vue'
export default {
    data () {
      var checkInit = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        if (!Number.isInteger(value)) {
          callback(new Error(''));
        } else {
          // if (value < 18) {
          //   callback(new Error('18'));
          // } else {
          //   callback();
          // }
          callback();
        }
      };
      var checkPeopleMoney = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        if (!Number.isInteger(value)) {
          callback(new Error(''));
        } else {
          // if (value < 18) {
          //   callback(new Error('18'));
          // } else {
          //   callback();
          // }
          callback();
        }
      };
      return {
        region:[], //
        storeArr:[],  //
        form:{
          brands:'',
          storeName:'',
          address:'',
          range:'',
          storeManager:'',
          storePhone:'',
          storeImgs:[],
          storeTimeAm:[new Date(), new Date()],
          storeTimePm:[new Date(), new Date()],
          timeType:'1',
          initPrice:'',
          peopleMoney:''
        },
        rules: {
          brands: [
            { required: true, message: '', trigger: 'blur' },
          ],
          storeName:[
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 10, message: ' 1  10 ', trigger: 'blur' }
          ],
          address: [
            { required: true, message: '', trigger: 'blur' }
          ],
          range:[
            { required: true, message: '', trigger: 'blur' }
          ],
          peopleMoney:[
             { required: true, validator: checkPeopleMoney, trigger: 'blur' }
          ],
          storeManager:[
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 10, message: ' 1  10 ', trigger: 'blur' }
          ],
          storePhone:[
            { required: true, message: '', trigger: 'blur' },
            {
              validator: function (rule, value, callback) {
                  var MobileRegex = /^1[0-9]{10}$/;
                  if (!MobileRegex.test(value)) {
                      callback(new Error(''))
                  } else {
                      callback();
                  }
                }, trigger: 'blur'
            }
          ],
          storeImgs:[
            { required: 'array', message: '', trigger: 'blur' }
          ],
          storeTimeAm:[
            { required: true, message: '', trigger: 'blur' }
          ],
          storeTimePm:[
            { required: true, message: '', trigger: 'blur' }
          ],
          timeType:[
            { required: true, message: '', trigger: 'blur' }
          ],
          initPrice:[
             { validator: checkInit, trigger: 'blur' }
          ]
        },
        timeTypes:[{
          value: '1',
          label: ''
        }],
        brands:[{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        data:[{
          brandsName:'',
          storeName:'',
          storeManager:'',
          phone:'13028107321',
          address:'',
          time:['8:00-12:00','14:00-20:00']
        },{
          brandsName:'',
          storeName:'2',
          storeManager:'',
          phone:'13028107321',
          address:'',
          time:['8:00-12:00','14:00-20:00']
        },{
          brandsName:'',
          storeName:'3',
          storeManager:'',
          phone:'13028107321',
          address:'',
          time:['8:00-12:00','14:00-20:00']
        },{
          brandsName:'',
          storeName:'4',
          storeManager:'',
          phone:'13028107321',
          address:'',
          time:['8:00-12:00','14:00-20:00']
        },{
          brandsName:'',
          storeName:'5',
          storeManager:'',
          phone:'13028107321',
          address:'',
          time:['8:00-12:00','14:00-20:00']
        },{
          brandsName:'',
          storeName:'6',
          storeManager:'',
          phone:'13028107321',
          address:'',
          time:['8:00-12:00','14:00-20:00']
        }],
        totalPages:10,
        pageSize:1,
        currentPage:1,
        search:'',
        city:'2',
        citysList:[{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        storesBox:false,
        mapVisible:false
      }
    },
    methods:{
      //
      indexMethod(index) {
        return index+1 + 10*(this.currentPage-1)
      },
      //
      submit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$stamp(null,this.form);
          } else {
            this.$stamp(null,'error submit!!');
            return false;
          }
        });
      },
      // 
      resetForm(formName) {
        this.region = []
        this.storeArr = []
        this.$refs[formName].resetFields();
      },
      //
      storeEdit(){
        this.$stamp(null,this.region)
        this.$stamp(null,this.storeArr)
        this.mapVisible = true
      },
      // 
      getPath(message){
        this.$stamp(null,message)
        this.storeArr = message.storeArr
        this.form.address = message.address
        this.region = message.region
        this.form.range = message.range
        this.mapVisible = message.mapVisible
      },
      //
      getStoreImg(val){
        this.$stamp(null,val)
        this.form.storeImgs = val
      },
      //
      chooseCity(){
        this.$stamp(null,this.city)
      },
      // 
      searchStore(){
        this.$stamp(null,this.search)
      },
      //
      edit(val){
        this.$stamp(null,val)
      },
      //
      addStores(){
        this.storesBox = true
      },
      //
      getCurrentPage(val){
        this.currentPage = val
      }
    },
    computed: {
      
    },
    components: {
      pages,
      imgs,
      maps
    },
    created(){
      
    }
}
</script>

<style>
  .brandsStore_head{
    display:flex;
    align-items:center;
    justify-content:space-between;
  }
  .brandsStore_chooseCity{
    background-color:#fff;
    color:#000;
    box-sizing:border-box;
    padding:9px;
    border-radius:5px 0 0 5px;
  }
  .brandsStore_inline_body .el-dialog__body{
    padding-top:0;
  }
  .brandsStore_inline_body .el-dialog__header{
    text-align:center;
    background-color:#ed6f00;
    padding-top:10px;
  }
  .brandsStore_inline_body .el-dialog__title{
    color:#fff;
  }
  .brandsStore_inline_body .el-dialog__headerbtn{
    top:12px;
  }
  .brandsStore_inline_body .el-dialog__headerbtn .el-dialog__close{
    color:#fff;
  }
  .brandsStore_options .el-input__inner{
    border:0;
    height:39px;
    line-height:39px;
    border-radius:0 5px 5px 0;
  }
  .brandsStore_options .el-select__caret{
    color:#000!important;
  }
  .brandsStore_storeNub{
    color:#000;
    box-sizing:border-box;
    padding:9px;
  }
  .brandsStore_addStore{
    background-color:#ed6f00;
    color:#fff;
    box-sizing:border-box;
    padding:7px 15px;
    cursor:pointer;
    margin-right:20px;
    border-radius:30px;
  }
  .brandsStore_search input{
    border:0;
    border-radius:30px;
  }
  .brandsStore_search .el-input__inner{
    height:33px;
  }
  .brandsStore_search .el-input__suffix{
    color:#FF9800;
  }
  .brandsStore_table thead tr th{
	  background-color: #494949;
    color:#ffffff;
  }
  .brandsStore_table thead tr th:nth-child(1){
    border-radius:5px 0 0 0;
  }
  .brandsStore_table thead tr th:nth-child(8){
    border-radius:0 5px 0 0;
  }
  .brandsStore_img li{
    width:100px!important;
    height:100px!important;
  }
  .brandsStore_img .el-upload-list{
    width:100px!important;
    height:100px!important;
    line-height: 1;
  }
  .brandsStore_img .el-upload--picture-card{
    width:100px!important;
    height:100px!important;
    line-height:100px;
  }
</style>



